import * as React from 'react';
import * as ReactDom from 'react-dom';
import { Version } from '@microsoft/sp-core-library';
import {
  BaseClientSideWebPart,
  IPropertyPaneConfiguration,
  PropertyPaneTextField
} from '@microsoft/sp-webpart-base';

import * as strings from 'DocumentListViewWebPartStrings';
import DocumentListView from './components/DocumentListView';
import { IDocumentListViewProps } from './components/IDocumentListViewProps';

export interface IDocumentListViewWebPartProps {
  siturl: string;
  filterlist: string;
  datalist: string;
  
}

export default class DocumentListViewWebPart extends BaseClientSideWebPart<IDocumentListViewWebPartProps> {

  public render(): void {
    const element: React.ReactElement<IDocumentListViewProps > = React.createElement(
      DocumentListView,
      {
        siteurl: this.context.pageContext.web.absoluteUrl,
        filterlist: this.properties.filterlist,
        datalist: this.properties.datalist,
        spHttpClient: this.context.spHttpClient
      }
    );

    ReactDom.render(element, this.domElement);
  }

  protected get dataVersion(): Version {
    return Version.parse('1.0');
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField('filterlist', {
                  label: "Filter List"
                }), 
                PropertyPaneTextField('datalist', {
                  label: "Data List"
                })
              ]
            }
          ]
        }
      ]
    };
  }
}
